// If you want to override variables do it here
@import 'variables';

@media (max-width: 767px) {
  body {
    padding-top: 50px;
  }
}
:root {
  --top-menu-height: 4em;
  --margin: 20px;
}

.top-menu {
  height: var(--top-menu-height);
}

.grid {
  width: 100%;
  display: grid;
  grid: var(--top-menu-height) / 1fr;
  grid-template-areas:
    'menu'
    'maincontent';
  // min-height: 100vh;
}

.menu {
  grid-area: menu;
  z-index: 100;
}

.main-content {
  grid-area: maincontent;
  background-color: #f9fafb;
}

.parent {
  display: inline-flex;
}

.side {
  transition: width 0.3s;
  width: 19em !important;
  overflow: hidden auto;
  padding-top: var(--top-menu-height);
  z-index: 99;
}

.small-side {
  width: 5em !important;
  flex-basis: 4em;
}

.body-content {
  position: absolute;
  right: 0;
  transition: all 0.3s;
  padding: 10px;
  width: calc(100% - 19em);
}

.small-content {
  padding: 10px;
  width: calc(100% - 5em);
}

.logo-space-menu-item {
  width: 14em;
}

.display-inline {
  display: inline-flex;
  align-items: center;
}

.logo-space {
  font-family: 'Roboto Light', serif;
  font-size: 24px;
  margin: 0 auto;
}

.logo-space img,
.display-inline img {
  margin-right: 8px;
}

.no-border::before {
  display: none;
}

.top-menu i.icon {
  margin: 0 !important;
}

.drop-left-padding {
  padding-left: 0 !important;
}

.label-on-corner {
  top: 0.8em !important;
  left: 78% !important;
}

.ui.form .field .ui.input,
.ui.form .fields .field .ui.input,
.ui.form .wide.field .ui.input {
  width: 100%;
  height: 2.5em;
}

.ui.form .field > .selection.dropdown {
  width: 100%;
}

.ui.checkbox label {
  font-size: 16px !important;
}

h1.ui.header {
  font-size: 2rem;
  color: $primary;
}
.full-loading {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
}

.half-loading {
  display: flex;
  align-items: center;
  padding: 30px;
  justify-content: center;
  z-index: 1040;
  background: rgba(194, 191, 191, 0.5);
}

.detail-form-frame {
  border-top-style: ridge !important;
  border-color: #ba242d !important;
  border-top-width: 2px !important;
  header,
  header > * {
    color: #ba242d;
    font-weight: bold;
  }
  label {
    font-weight: 700;
    display: flow-root;
    padding: 15px;
    font-size: 20px;
    span:first-child {
      margin-right: 12px;
      color: #666;
      text-align: right;
      width: 33.333333%;
      float: left;
      font-weight: 400;
    }
  }
}

.profile-header {
  font-size: 22px;
  font-weight: bold;
  color: #0d787a;
}

.customInput {
  input {
    font-size: 20px !important;
    padding: 5px !important;
    font-weight: 400;
    width: 20vw;
  }
}

.skeleton-wrapper {
  padding-top: 30px !important;
}
